    public search(): void {
      let arr: any = []
      this.lists.forEach((item: any, index: number) => {
        if (item.name.includes(this.keyWords)) {
          this.currentIndex = index
          arr.push(item)
        }
      })
      this.lists = arr
      if (!this.keyWords) {
        this.lists = JSON.parse(localStorage.getItem('lists'))
      }
    }
