import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import AppleHomeUI from 'applehome-ui'
Vue.use(AppleHomeUI)
import 'applehome-ui/dist/index.css'
import Slide from './components/Basic/Slide'
Vue.component('ap-slide', Slide);

Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
